body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
    position: fixed;
    margin: 0;
    padding: 0;
}

.App {
    cursor: pointer;
    text-align: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: #282c34;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    height: 100vh;
    width: 100vw;
}

.Spacer {
    flex: 1 1;
    flex-direction: column;
    font-size: calc(20px + 4vmin);
    display: flex;
    align-items: stretch;
}

.Game {
    height: 100vmin;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
}

.Row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.Cell {
    margin: 5px;
    background-color: #FFFFFF;
    border-width: 5px;
    border-color: #000000;
    border-style: solid;
}

